    width: 100%;
    left: 0;
    text-align: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
    align-items: center;
    justify-content: center;
  }

  .seccion-perfil-usuario .redes-sociales .boton-redes + .boton-redes {
    margin-left: 1rem;
    margin-top: 0;
  }
}

